.entries-loader {
  height: 100%;
  overflow: hidden;
  background: rgba(255,255,255, .9);
  position: absolute;
  left: 0;
  right: 0;
  /* margin-left: -25px;
  margin-right: -25px; */
  z-index: 1;
  /* height: 100%; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transition: all .2s linear;
  -o-transition: all .2s linear;
  transition: all .2s linear;
  /* display: none; */
  opacity: 0;
}

@-webkit-keyframes slideIn {
  from {height: 10px;}
  to {height: 100%;}
}

@keyframes slideIn {
  from {height: 10px;}
  to {height: 100%;}
}
@-webkit-keyframes slideOut {
  from {height: 100%;}
  to {height: 10px;}
}
@keyframes slideOut {
  from {height: 100%;}
  to {height: 10px;}
}
@-webkit-keyframes fadeIn {
  from {opacity: 0;}
  to {opacity: 1;}
}
@keyframes fadeIn {
  from {opacity: 0;}
  to {opacity: 1;}
}
@-webkit-keyframes fadeOut {
  from {opacity: 1;}
  to {opacity: 0;}
}
@keyframes fadeOut {
  from {opacity: 1;}
  to {opacity: 0;}
}

#scrollTarget {
  background: #E0F2F1;
}

.showed {
  /* animation: fadeIn .5s ease 5s forwards; */
  -webkit-transform: opacity .2s linear;
      -ms-transform: opacity .2s linear;
          transform: opacity .2s linear;
}
.hidden {
  /* animation: fadeOut .5s ease 5s backwards; */
  -webkit-transform: opacity 2s linear;
      -ms-transform: opacity 2s linear;
          transform: opacity 2s linear;
}


